@import "~antd/dist/antd.css";

.App {
  height: 100%;
  width: 100%;
  user-select: none;
  /* background-color: rgb(240, 240, 240); */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app-status {
  padding: 1em;
}

.app-page {
  overflow-y: auto;
  flex: auto;
}

.controller {
  position: relative;
  height: 100%;
  width: 100%;
}

.controller-form {
  position: fixed;
  bottom: 0;
  padding: 0 1em;
}

.backward-button {
  position: fixed;
  bottom: 30vh;
  right: 5vw;
}

.forward-button {
  position: fixed;
  bottom: 55vh;
  right: 5vw;
}
.left-button {
  position: fixed;
  bottom: 40vh;
  left: 5vw;
}

.right-button {
  position: fixed;
  bottom: 40vh;
 left: 15vw;
}

.player {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: -1;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.canvas {
  height: 100%;
}

.ai-example-list {
  background-color: #fff;
  padding: 5vw;
}
